.gpt3__header {
    display: flex;
}

.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    letter-spacing: -0.04rem;
    line-height: 75px;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;

    color: var(--color-text);

    margin-top: 1.5rem ;
}
.gpt3__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}
.gpt3__header-content__input input {
   flex: 2;
   width: 100%;
   min-width: 50px;
   font-family: var(--font-family);
   font-size: 20px;
   line-height: 27px;
   background: var(--color-footer);
   border: 2px solid var(--color-footer);
   padding: 0 1rem;
   outline: none;
   color: white;
   border-radius: 5px 0px 0px 5px;

   border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt3__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: #FF4820;
    color: #FFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}

.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 2rem;
}

.gpt3__header-content__people img {
    width: 181.79px;
    height: 38px;
}

.gpt3__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #FFF;
    text-align: center;

}

.gpt3__header-image {
    flex: 1;
    display: flex;
    overflow: hidden;
    position: relative;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-out;
    animation: parallaxFloat 6s ease-in-out infinite;
}

.gpt3__header-image {
    flex: 1;
    display: flex;
    overflow: hidden;
    position: relative;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease-out;
    animation: parallaxFloat 6s ease-in-out infinite;
}

@keyframes parallaxFloat {
    0%, 100% {
        transform: translateY(0px) scale(1);
    }
    50% {
        transform: translateY(-20px) scale(1.02);
    }
}

.gpt3__header-image:hover img {
    transform: scale(1.02);
}

@media screen and (max-width: 1050px){
    .gpt3__header{
        flex-direction: column;

    }
    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px){
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size:  16px;
        line-height: 24px;
    }
    .gpt3__header-content__people {
        flex-direction: column;
    }
    .gpt3__header-content__people p{
        margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
    }
    .gpt3__header p{
        font-size:  14px;
        line-height: 24px;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}